export const groupColors = () => {
  const groups = {};
  let currentRowIndex = 0;
  let currentIndex = 0;
  for (let i = 0; i < rawColors.length; i += 1) {
    if (rawColors[i].name === 'NextPage') {
      currentRowIndex += 1;
      currentIndex = 0;
    } else {
      if (i !== 0 && (currentIndex + 1) % 8 === 0) {
        currentRowIndex += 1;
        currentIndex = 0;
      }
      if (!groups[currentRowIndex]) {
        groups[currentRowIndex] = [];
      }
      groups[currentRowIndex].push(rawColors[i]);
      currentIndex += 1;
    }
  }
  return groups;
};

export const colors = rawColors.filter((color) => color.name !== 'NextPage');

export const getProps = (opt = '*') => {
  const keys = Object.keys(colors[0]);
  if (opt === '*') {
    return keys;
  }
  return keys.filter((k) => k === opt);
};

export const filterColor = (searchTerm = '', props = []) => (
  colors.filter((color) => (
    props.some((prop) => new RegExp(searchTerm, 'gi').test(color[prop]))
  ))
);

export const colorGroups = groupColors();
